<template>
  <div class="page">
    <div flex style='background:#fff;padding:.5rem 0;'>
      <div flex-box="0" class='mr-10 t-sm t-right' style='width:20vw;line-height:46px;'>
        <span></span>
      </div>
      <div flex-box="1">
        <div flex class="t-xs t-center">
          <div flex-box="1">
            <span :class="{'t-blue':progress>=0}"></span>
          </div>
          <div flex-box="1">
            <span :class="{'t-blue':progress>1}"></span>
          </div>
          <div flex-box="1">
            <span :class="{'t-blue':progress>2}"></span>
          </div>
        </div>
        <mt-progress :value="[0,33,66,100][progress]" :bar-height="5"></mt-progress>
      </div>
    </div>
    <!-- {{userInfo.status}} -->
    <div v-if='userInfo.status<=1'>
      <div class="t-center" style='padding:.5rem;'>
        <div class="mb-10"></div>
        <a :href="userInfo.entry_form_example_url" class="link t-sm">.doc>></a>
      </div>
      <div>
        <mt-field-upload @success='(data)=>{ 
          params.avatar = data.id;
          params.avatar_file = data.url}' title='1'></mt-field-upload>
        <mt-field label="" placeholder="" v-model="params.cn_name"></mt-field>
        <mt-field label="" placeholder="" v-model="params.en_name"></mt-field>
        <label class="mint-cell mint-field"><!----> 
          <div class="mint-cell-left"></div> 
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title"><!----> 
            <span class="mint-cell-text"></span> <!----></div> 
            <div class="mint-cell-value is-link">
              <select v-model='params.sex' class="mint-field-core" placeholder=''>
                <option value='M'>M</option>
                <option value='F'>F</option>
              </select>
            </div>
          </div> 
          <div class="mint-cell-allow-right"></div>
        </label>
        <mt-field label="" placeholder="" type="date" v-model="params.birthday"></mt-field>
        <mt-field label="" placeholder="" type="text" v-model="params.college"></mt-field>
        <mt-field label="" placeholder="" type="text" v-model="params.contacts_phone"></mt-field>
        <mt-field label="" placeholder="" type="text" v-model="params.email"></mt-field>
        <mt-field label="" placeholder="" type="text" v-model="params.idcard"></mt-field>
        
        <mt-field label="" placeholder="" type="text" v-model="params.edu_num"></mt-field>
        <mt-field-upload @success='(data)=>{ 
          params.edu = data.id;
          params.edu_file = data.url}' title=''></mt-field-upload>
        <mt-field-upload @success='(data)=>{ 
          params.degree = data.id;
          params.degree_file = data.url}' title=''></mt-field-upload>
        <mt-field-upload @success='(data)=>{ 
          params.idcard_front = data.id;
          params.idcard_front_file = data.url}'  title=''></mt-field-upload>
        <mt-field-upload @success='(data)=>{ 
          params.idcard_reverse = data.id;
          params.idcard_reverse_file = data.url}'  title=''></mt-field-upload>
        <label class="mint-cell mint-field"><!----> 
          <div class="mint-cell-left"></div> 
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title"><!----> 
            <span class="mint-cell-text"></span> <!----></div> 
            <div class="mint-cell-value is-link">
              <select v-model='params.period' class="mint-field-core" placeholder=''>
                <option v-for='item in userInfo.period' :key='item.id' :value='item.id'>{{item.name}}</option>
              </select>
            </div>
          </div> 
          <div class="mint-cell-allow-right"></div>
        </label>
        <mt-field label="" placeholder="" type="textarea" rows="2" v-model="params.addr"></mt-field>
      </div>

      <div class="t-xs" style='padding:.5rem;'>
        <p>1</p>
        <p>2</p>
        <p style='text-indent:1em;'>jpg600px800px1MB</p>
      </div>
      <div flex class="padding">
        <div flex-box='1' class="mr-10">
          <mt-button type='danger' @click.native='doSubmit' size="large"></mt-button>
        </div>
        <div flex-box='1'>
          <mt-button type='default' @click.native='doSave' size="large"></mt-button>
        </div>
      </div>
    </div>

    <div v-else>
        <mt-cell title="" :value="params.cn_name"></mt-cell>
        <mt-cell title="" :value="params.en_name"></mt-cell>
        <mt-cell title="" :value="params.sex=='M'?'':''"></mt-cell>
        <mt-cell title="" :value="params.birthday"></mt-cell>
        <mt-cell title="" :value="params.college"></mt-cell>
        <mt-cell title="" :value="params.contacts_phone"></mt-cell>
        <mt-cell title="" :value="params.email"></mt-cell>
        <mt-cell title="" :value="idcard"></mt-cell>
        <mt-cell title="" :value="params.edu_num"></mt-cell>
        <mt-cell title="" is-link :to="params.edu_file"></mt-cell>
        <mt-cell title="" is-link :to="params.degree_file"></mt-cell>
        <mt-cell title="" is-link :to="params.idcard_front_file"></mt-cell>
        <mt-cell title="" is-link :to="params.idcard_reverse_file"></mt-cell>
        <!-- <mt-cell title="" value=""></mt-cell> -->
        <mt-cell title="" :value="params.addr"></mt-cell>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import mtFieldUpload from "@/components/mt-field-upload.vue";
export default {
  components: {
    mtFieldUpload
  },
  data() {
    return {
      progress: 1,
      params: {
        addr: "",
        birthday: "",
        college: "",
        contacts_phone: "",
        edu: "",
        edu_num: "",
        edu_file: "",
        email: "",
        entry_form: "",
        idcard: "",
        period: "",
        degree: "",
        degree_file: "",
        sex: "M",
        cn_name: "",
        en_name: "",
        avatar: "",
        avatar_file: "",
        idcard_front: "",
        idcard_front_file: "", //
        idcard_reverse: "",
        idcard_reverse_file: "" //
      }
    };
  },
  computed: {
    ...mapState({
      userInfo: state => state.userInfo
    })
  },
  watch: {
    userInfo: {
      handler({ user }) {
        //let { user } = val;
        this.params = user;
      },
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      submitUserInfo: "submitUserInfo",
      addUserInfo: "addUserInfo",
      fileUpload: "fileUpload",
      getUserSign: "getUserSign",
      _getUserSignPayinfo: "getUserSignPayinfo"
    }),
    doSubmit() {
      this.submitUserInfo({ ...this.params, ...{ entry_form: -1 } }).then(
        ({ code, data_msg, msg }) => {
          this.$toast(msg);
          code == 200 && this.getUserSign();
          data_msg && this.errorAlert(data_msg);
        }
      );
    },
    doSave() {
      this.addUserInfo({ ...this.params, ...{ entry_form: -1 } }).then(
        ({ code, data_msg, msg }) => {
          this.$toast(msg);
          code == 200 && this.getUserSign();

          data_msg && this.errorAlert(data_msg);
        }
      );
    },
    errorAlert(data_msg) {
      let [key] = Object.keys.call(this, data_msg);
      const errs = {
        edu_num: ""
      };

      this.$toast(`${errs[key] || ""}`);
    },
    getUserSignPayinfo() {
      this._getUserSignPayinfo().then(res => {
        res.status == 0 &&
          this.$messagebox.alert(",").then(() => {
            this.$router.push("./signup");
          });
      });
    }
  },
  mounted() {
    this.getUserSign().then(res => {
      this.progress = res.status;
      if (res.status == 3) {
        this.getUserSignPayinfo();
      }
    });
  }
};
</script>
 <style lang="less" scoped>
.page {
  margin: 40px 0 50px;
}
</style>
<style lang="less">
.is-textarea {
  .mint-cell-title {
    margin-top: -50px;
  }
}
</style>
