//import cookie from '../jPlugins/jquery.cookie.min'
//import $ from 'jquery';
import storage from "./storage";
import api from "./api";
import tools from "./tools";
// import Cookies from "js-cookie";
import { getToken, removeToken } from "./auth";

export default {
  install(Vue) {
    Vue.mixin({
      data() {
        let token = getToken();
        return {
          Token: token
        };
      },
      filters: {
        htmlfilter(s) {
          s = s.replace(/<\s*script[^>]*>(.|[\r\n])*?<\s*\/script[^>]*>/gi, "");
          s = s.replace(/<\s*style[^>]*>(.|[\r\n])*?<\s*\/style[^>]*>/gi, "");
          s = s.replace(/<\/?[^>]+>/g, "");
          s = s.replace(/\&[a-z]+;/gi, "");
          s = s.replace(/\s+/g, "\n");
          return s;
        },
        money(val, accuracy = 1) {
          return `${(val * accuracy).toFixed(2)}`;
        }
      },
      methods: {
        doLogout() {
          removeToken();
          //$cookie.clearCookie("userToken", "", -1);
          //$.cookie("userToken", "", -1);
          //console.log(this.$router,1111);
          this.$router.go();
        },
        openNew(link) {
          // var e = document.createElement("a");
          // document.body.appendChild(e);
          // e.href = link;
          // e.target = "_new";
          // e.click();
          // document.body.removeChild(e);
          window.open(link);
        },
        initWeiXin(jsApiList = [], callback) {
          api.getWeiXinConfig().then(() => {
            wx.config({
              beta: true, // wx.invokejsapi
              debug: true, // ,apialertpclogpc
              appId: "", // corpID
              timestamp: 0, // 
              nonceStr: "", // 
              signature: "", // 1
              jsApiList: jsApiList // JSJS2
            });
            wx.ready(() => {
              callback && callback(wx);
            });
          });
        },
        bindWeiXinOpenId(url) {
          if (location.host === "test.chaozhiedu.com") {
            return this.$router.push({ name: "index" });
          }

          if (this.$tools.isWechat()) {
            const token = getToken();
            let redirect_uri = encodeURIComponent(url || window.location.href);
            window.location.href = `//aci-api.chaozhiedu.com/api/weixinauth?token=${token}&url=${redirect_uri}`;
          } else {
            return this.$router.push({ name: "index" });
          }
        },
        weixinPay(config, callback) {
          if (!config) {
            this.$toast("config");
            return false;
          }

          function onBridgeReady() {
            WeixinJSBridge.invoke("getBrandWCPayRequest", config, function(
              res
            ) {
              if (res.err_msg == "get_brand_wcpay_request:ok") {
                callback && callback();
              } // ,res.err_msg    ok
            });
          }
          if (typeof WeixinJSBridge == "undefined") {
            if (document.addEventListener) {
              document.addEventListener(
                "WeixinJSBridgeReady",
                onBridgeReady,
                false
              );
            } else if (document.attachEvent) {
              document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
              document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
            }
          } else {
            onBridgeReady();
          }
        }
      }
    });

    //Vue.prototype.$tools = prototypes;
    //Vue.prototype.$regex = regex;
    //Vue.prototype.$api = api;
    Vue.prototype.$storage = storage;

    Vue.prototype.$tools = tools;
  }
};
